@import "../../global.scss";

.menu{
    width:300px;
    height:100vh;
    background-color: white;
    position: fixed;
    top: 0px;
    left: 0px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 1s ease;

    &.active{
        left:-300px;
        background-color: $mainColor;
    }

    ul {
        margin: 0;
        padding: 0;
        list-style: none;
        font-size: 30px;
        font-weight: 300;
        color: $mainColor;

        li{
            margin-bottom: 25px;
            a{
                font-size: inherit;
                color: inherit;
                text-decoration: none;
            }
            &:hover{
                font-weight: 700;
            }
        }
    }
}